 .valBanInputs {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding:  300px 100px;
}
 .valBanInputs p{
    font-size: 20px;
    font-weight: 600;
    letter-spacing: 5px;
    color: white;
    padding-bottom: 30px;
    text-shadow: 0 10px 10px rgba(0, 0, 0, 0.4);
}
 .valBanInputs h1{
    color: white;
    text-transform: uppercase;
    font-size: 100px;
    padding-bottom: 30px;
    font-weight: 600;
    text-shadow: 0 10px 10px rgba(0, 0, 0, 0.4);
}
.lolBan .valBanInputs h1{
    color: #a2fdff;
    text-transform:none;
    font-size: 70px;
    padding-bottom: 30px;
    font-weight: 600;
    text-shadow: 0 10px 10px rgba(0, 0, 0, 0.4);
}
 .valBanInputs button{
    width: 200px;
    height: 50px;
    cursor: pointer;
    border: 1px solid #d13738;
    background: #d13738;
    color: white;
    border-radius: 4px;
    transition: .6s;
    font-size: 14px;
    margin-right: 30px;
    font-weight: 600;
    overflow: hidden;
    position: relative;
    box-shadow: 0 10px 10px rgba(0, 0, 0, 0.4);
    text-transform: uppercase;
    letter-spacing: .5px;
}

 .valBanInputs button:hover{
    background-color: #e3dad1;
    color: #d13738;
}
