.gameDiv1{
    background-color: #1e1e1f;
}
.gameDiv1 h1{
    color: white;
    padding: 25px 60px;
    font-size: 45px;
    text-shadow: 0 10px 20px rgba(0, 0, 0, 0.4);
}
.gameDiv1 .gameBanner{
    display: flex;
    justify-content: center;
    padding-bottom: 60px;
} 
.gameDiv1 .gameBanner img{
    width: 70%;
    background-size: cover;
    border-radius: 10px;
    border: 3px solid #1e1e1f;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.4);
    transition: .6s;
}
.gameDiv1 .gameBanner img:hover{
    border: 3px solid  #d13738;


}
.gameDiv2{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    }
    
    .gameDiv2 .gameColumn1{
    display: flex;
    background-color: #3f3f3f;
    align-items: center;
    overflow-x:auto;
    width: 100%;
    text-align: center;
}
.gameDiv2 .gameColumn1::-webkit-scrollbar{
    width: 0;
}
.gameDiv2 .gameColumn1 .gameCard {
    display: flex;
    position: relative;
    width: 450px;
    margin: 0 30px;
    outline: none;
    border: none;
    margin: 30px 5px;
    margin-left: 30px;
    border-radius: 7px;
    box-shadow: 0 10px 10px rgba(0, 0, 0, 0.4);
    cursor: pointer;

}
.gameColumn1 .gameCard  img{
    border-radius: 7px;
    width: 100%;
    border: 3px solid  #3f3f3f;
    transition: .6s;
}
.gameColumn1 .gameCard  img:hover{
    border: 3px solid  rgb(127, 124, 124);


}
.gameDiv3{
    background-color: #1e1e1f;
    padding: 40px;
    
}
.gameDiv3 p {
    color: white;
    font-size: 20px;
    padding-bottom: 40px;
}
.gameDiv3 h2{
    color: white;
    padding:   0 30px 40px;
    font-size: 40px;

}
.gameDiv3 .fileUpload {
    outline: none;
    width: 250px;
    height: 60px;
    border-radius: 8px;
    align-items: center;
    text-align: center;
    box-shadow: 0 10px 10px rgba(0, 0, 0, 0.4);
    background-color: #3f3f3f;
    transition: .5s;
    font-size: 14px;
    color: white;
    padding-right: 10px;
}

::-webkit-file-upload-button{
    transition: .5s;
    border-radius: 8px;
    border: none;
    outline: none;
    background-color: #d13738;
    color: #3f3f3f;
    padding:18px;
    font-size:20px ;
    color: white;
    margin-right: 10px;
}
::-webkit-file-upload-button:hover{
    background-color: #3f3f3f;
}
@media (max-width: 768px){

.gameDiv1 h1{
    font-size: 28px;
}
.gameDiv1 .gameBanner img{
    width: 90%;

}
 .gameDiv2 .gameColumn1 .gameCard{
    width: 250px;
 }
 .gameDiv3{
    padding: 30px;
 }
  .gameDiv3 h2{
    padding: 20px;
    font-size: 28px;
  }
    .gameDiv3 p{
    font-size: 16px;

    }
    .gameDiv3 .fileUpload {
        width: 250px;
        height: 50px;
    }
    ::-webkit-file-upload-button{
    font-size:20px ;
    font-size:16px
    }
}